.code {
    background-color: lightgray;
    border-radius: 8px;
    color: rgb(53, 53, 53);
    padding: 12px;
}

.code span {
    display: block;
}

.indent {
    margin-left: 16px;
}

.iconWrapper {
    display: flex;
    gap: 8px;
    justify-content: flex-end;
}

.icon {
    color: darkgray;
    cursor: pointer;
    opacity: 0.5;
    transition: 0.3s;
}

.icon:hover {
    opacity: 1;
    transition: 0.3s;
}

.previewIcon {
    right: 8px;
    top: 8px;
}

.clipboardIcon {
    right: 36px;
    top: 8px;
}

.copyButton {
    all: unset;
}

