.preview {
    background-color: white;
    border-radius: 8px;
    color: rgb(53, 53, 53);
    margin: 16px 0;
    min-height: 42.4px;
    padding: 12px;
}

.iconWrapper {
    display: flex;
    gap: 8px;
}

.iconWrapper span {
    font-weight: bold;
}

.icon {
    color: lightgray;
    cursor: pointer;
    opacity: 0.5;
    transition: 0.3s;
}

.icon:hover {
    opacity: 1;
    transition: 0.3s;
}

.codeIcon {
    margin-left: auto;
}

.formField {
    display: block;
    margin-bottom: 16px;
    width: 100%;
}

.formField span {
    display: block;
}

.formField input {
    width: 100%;
}