.appView {
    display: grid;
    grid-template-columns: 12;
    grid-template-rows: 12;
    padding: 12px 24px;
    width: 100%;
}

.appViewHeader {
    align-items: center;
    display: flex;
    gap: 24px;
    grid-column-start: 1;
    grid-column-end: 12;
    grid-row-start: 1;
    grid-row-end: 2;
}

.appViewHeaderItem {
    align-items: center;
    display: flex;
    gap: 12px;
}

.appViewRightPanel {
    display: inline-block;
    width: 150px;
}

.appViewFields {
    border-bottom: 1px solid lightgray;
    margin-bottom: 12px;
    padding-bottom: 12px;
}

.appViewFormEditor {
    display: flex;
    gap: 8px;
    grid-column-start: 1;
    grid-column-end: 12;
    grid-row-start: 2;
    grid-row-end: 10;
}

.appViewForm {
    display: inline-block;
    min-height: 1px;
    width: calc(100% - 150px);
}

.appViewFooter {
    grid-column-start: 1;
    grid-column-end: 12;
    grid-row-start: 10;
    grid-row-end: 12;
}
