.draggable {
    align-items: center;
    border: 1px solid lightgray;
    border-radius: 8px;
    cursor: grab;
    display: flex;
    margin: 4px 0;
    padding: 12px 24px;
    position: relative;
}

.dragIcon {
    color: lightgray;
    position: absolute;
    left: 0;
}

.dragIcon2 {
    left: 4px;
}